.header{width:100%;height:60px;background-color:#0e3b51;display:flex;justify-content:space-around;align-items:center;position:relative}.header form{width:50%;display:flex;justify-content:center}.header form .search{width:100%;max-width:30rem;position:relative;display:flex}.header form .search .searchInput{width:100%;border:2px solid #00b2ff;border-right:none;padding:5px;background:transparent;height:100%;height:2.5rem;letter-spacing:1px;text-transform:capitalize;border-radius:5px 0 0 5px;outline:none;color:#e9e9e9;font-size:1.25rem}.header form .search .searchInput:focus{color:#ffffff}.header form .search .searchButton{display:flex;justify-content:center;align-items:center;width:45px;height:2.5rem;border:1px solid #00b2ff;background:#00b2ff;color:#fff;border-radius:0 5px 5px 0;cursor:pointer}.wrapper{display:inline-flex;height:60%;width:20%;max-width:130px;align-items:center;justify-content:space-evenly;border-radius:5px}.wrapper input[type="radio"]{display:none}.wrapper .option{height:70%;width:100%;display:flex;align-items:center;justify-content:space-evenly;margin:0 5px;border-radius:5px;padding:5px;padding:0.9rem 0;cursor:pointer;border:1px solid #c1c1c1;transition:all 0.3s ease}.wrapper .option span{font-size:1.3rem;font-weight:600;letter-spacing:1px;color:#c1c1c1}.wrapper .option .dot{height:10px;width:10px;background:#ffffff;background:#c1c1c1;border-radius:50%;position:relative}.wrapper .option .dot::before{position:absolute;content:"";top:2px;left:2px;width:6px;height:6px;background:#ffffff;border-radius:50%;opacity:0;-webkit-transform:scale(1.5);transform:scale(1.5);transition:all 0.3s ease}.option__1:checked:checked ~ .option-1,.option__2:checked:checked ~ .option-2{border:none;padding:0.9rem 0.6rem;background:linear-gradient(140deg, #007afb 9.38%, #e500f5 100%)}.option__1:checked:checked ~ .option-1 .dot,.option__2:checked:checked ~ .option-2 .dot{background:#007afb}.option__1:checked:checked ~ .option-1 .dot::before,.option__2:checked:checked ~ .option-2 .dot::before{opacity:1;-webkit-transform:scale(1);transform:scale(1)}.option__1:checked:checked ~ .option-1 span,.option__2:checked:checked ~ .option-2 span{color:#ffffff}.weather-header{width:100%;max-width:45rem;height:7rem;display:flex;justify-content:space-between;align-items:center;border-radius:1.5rem;padding:0.2rem 0;color:white;margin-top:2rem;background:linear-gradient(92.49deg, #1b3c4e 2.58%, #0c2a38 94.5%)}.weather-header .location{display:flex;flex-direction:column;justify-content:center;align-items:center;width:33.33%;align-items:flex-start;margin-left:2rem;padding:0 1rem 0 0}.weather-header .location .city{font-size:1.3rem;font-weight:400;letter-spacing:1px;word-wrap:break-word}.weather-header .location .contury{color:#727272;text-align:left}.weather-header .deg-status{width:33.33%;display:flex;flex-direction:column;justify-content:center;align-items:center}.weather-header .deg-status span{display:block}.weather-header .deg-status .deg{font-size:2.9rem;font-weight:500;letter-spacing:-1px}.weather-header .deg-status .status{padding:0.1rem 0.5rem;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;border-radius:5px;font-weight:500;background:linear-gradient(275.05deg, #510056 5.33%, #8a0093 97.46%);letter-spacing:1px}.weather-header .img-box{width:33.33%;display:flex;flex-direction:column;justify-content:center;align-items:center;align-items:flex-end}.weather-header .img-box img{margin-right:2rem;width:50%}.weather-body{margin-top:0.5rem;width:100%;max-width:40rem;border-radius:1rem;background:linear-gradient(92.49deg, #1b3c4e 2.58%, #0c2a38 94.5%)}.today-forecast-section{padding:0.5rem 0.6rem}.today-forecast-section img{width:2.8rem}.today-forecast-section h6{text-align:center;font-size:2rem;font-weight:500;margin:0.5rem 0.4rem 0;color:#8a9bae;letter-spacing:1px}.today-forecast-section .today-forecast-weather{border-radius:1rem;color:#e1e6ec;display:flex;padding:1rem;overflow-x:auto;white-space:nowrap;text-align:center}.today-forecast-section .today-forecast-weather div:last-child{margin:0 0 0 0}.today-forecast-section .today-forecast-weather .single-hour-forecast{margin-right:2rem;padding:0.1rem}.today-forecast-section .today-forecast-weather .single-hour-forecast p:first-child{margin-bottom:0.3rem}.today-forecast-section .today-forecast-weather .single-hour-forecast p:last-child{letter-spacing:1px;padding-left:0.5rem;font-size:1.1rem;margin-top:0.1rem;font-weight:600}.today-forecast-section .today-forecast-weather .single-hour-forecast p{text-align:center}.today-forecast-section .today-forecast-weather div:last-child{margin:0}.today-forecast-section .today-forecast-weather::-webkit-scrollbar{height:3px}.today-forecast-section hr{position:relative;left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%);margin-top:1rem;height:1px;border:none;background-color:#807f7f;width:80%}.forecast-section{padding:1rem 2rem;color:#eeeff0}.forecast-section div{display:flex;justify-content:left;align-items:center;height:5rem;border-bottom:1px solid #707070}.forecast-section div p{font-size:1.2rem;font-weight:600}.forecast-section div p:first-child{font-size:1.5rem;font-weight:400;width:50rem;text-transform:capitalize}.forecast-section div img{height:3.5rem;margin-right:20%}.forecast-section div p:last-child{color:#9c9c9c;margin-left:5%}.details-section{padding:0 1rem 2rem 1rem;margin-top:2rem}.details-section h6{text-align:center;font-size:2rem;font-weight:500;margin-bottom:1rem;color:#8a9bae;letter-spacing:1px;text-transform:capitalize}.details-section .details{display:grid;grid-template-columns:repeat(auto-fit, 1fr);grid-gap:10px;gap:10px}.details-section .card{background-color:#001a23;display:flex;color:white;flex-direction:column;align-items:center;padding:0.7rem;border-radius:0.5rem}.details-section .card:first-child{grid-column:1/3}.details-section .card .card-heading{width:100%;display:flex;justify-content:space-between;align-items:center;margin-bottom:1rem}.details-section .card .card-heading img{width:35px;height:35px}.details-section .card .card-heading .detail-title{font-size:1.3rem;letter-spacing:1px;color:#a6c2e2}.details-section .card .detail-content{font-size:1.2rem}.loading-city{margin:0;background:#ababab;width:55%;height:2rem;border-radius:.2rem}.loading-country{margin:.3rem;background:#ababab;width:2rem;height:1rem;border-radius:.1rem}.loading-degree{margin:0 5rem 0.3rem 0;background:#ababab;width:55%;height:3rem;border-radius:.1rem}.loading-condition{margin:0 5rem 0rem 0;background:#ababab;width:30%;height:1rem;border-radius:.1rem}.loading-img{margin:2rem;background:#ababab;width:5rem;height:5rem;border-radius:.5rem}.loading-hour-forecast{padding:0 2rem;margin:1rem;background:#ababab;width:3rem;height:5rem;border-radius:.1rem}.loading-day-forecast{margin:.5rem;background:#ababab;width:100%;height:0rem;border-radius:.5rem}@media (max-width: 650px){.header .logo{width:5rem}.header .wrapper{width:6rem}.weather-header{border-radius:1rem}.weather-header .location{margin-left:1rem}.weather-header .deg-status{width:37%}.weather-header .img-box{width:6rem}.weather-header .img-box img{margin-right:0;width:100%}.weather-body .forecast-section{padding:1rem 1rem 0 1rem}.weather-body .details-section{margin-top:1rem}}.error-box{display:flex;align-items:center;justify-content:center;position:absolute;background-color:#d19b9b;padding:0.5rem 1.5rem;border-radius:5px;bottom:-2.8rem;z-index:4;transition:all 200ms ease-in-out;transition-delay:5s}.error-box::before{content:"";position:absolute;width:25px;height:25px;background-color:#d19b9b;top:-12px;-webkit-transform:rotate(45deg);transform:rotate(45deg);z-index:2}.error-box .error-msg{color:#970000;font-size:1.2rem;letter-spacing:1px}

